@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*App*/
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}
body {
    background-color: #fff;
}

/*Nav*/
nav {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 61px;
    background-color: #fff;
    padding-inline: 50px;
}
.logo {
    color: black;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.nav-links {
    display: flex;
    justify-content: space-around;
    width: 40%;
}
.nav-links a {
    width: 100%;
    list-style: none;
    min-width: 40px;
    text-align: center;
    padding: 20px;
    cursor: pointer;
    color: black;
    text-decoration: none;
}
.nav-links a:hover {
    color: #adb5bd;
}
.menu-icon {
    display: none;
}
.nav-links > .active {
    color: #adb5bd;
}

/*Home*/
.home {
    margin-bottom: 50px;
    background: linear-gradient(to bottom, #A5A5A5 0%, #A5A5A5 40%, #fff 40%, #fff 100%);
}
.profile {
    padding: 10px;
    display: grid;
    justify-content: center;
}
.card-profile {
    margin: auto;
    box-shadow: -10px 10px 20px rgba(0,0,0,0.3);
}
.card-profile-top {
    width: 300px;
    height: 350px;
    background-color: #CCCCCC;
}
.profile-picture-container {
    padding: 20px;
    display: flex;
    justify-content: center;
}
.profile-picture{
    width: 150px;
    background-color: #fff;
    border: 5px solid #F2F2F2;
    border-radius: 100%;
}
.profile-name {
    font-size: 14px;
    text-align: center;
    margin-bottom: 15px;
}
.profile-job {
    display: none;
}
.card-profile-bottom {
    display: flex;
    justify-content: center;
    width: 300px;
    height: 50px;
    background-color: #fff;
}
.logo-link-container {
    cursor: pointer;
    padding: 7px;
    margin-inline: 15px;
    font-size: 25px;
}
.logo-link-container a {
    color: black;
}
.download-button-container-small {
    display: flex;
    justify-content: center;
    padding: 20px;
}
.download-button {
    color: #FFF;
    font-size: 18px;
    padding: 7px 15px;
    background-color: #0062b0;
    border-radius: 15px;
    text-decoration: none;
}
.download-button:hover {
    cursor: pointer;
    background-color: #4682b4;
    transition: background-color 0.2s ease-in;
}
.download-button-container-big {
    display: none;
}
.profile-text {
    margin-top: 25px;
}
.profile-text p {
    color: #333333;
    margin-top: 15px;
}
.profile-desc {
    padding-inline: 25px;
    padding-block: 15px;
}
.profile-desc-top h1 {
    font-size: 40px;
}
.profile-desc-top p {
    margin-top: 10px;
    font-weight: bold;
}

/* Logo-profiles */
.fa-github:hover {
    color: #333;
    transition: color 0.2s ease-in;
}
.fa-instagram:hover {
    color: #fb3958;
    transition: color 0.2s ease-in;
}
.fa-linkedin-in:hover {
    color: #0072b1;
    transition: color 0.2s ease-in;
}

/* line */
.line-behind-header {
    margin: auto;
    border: none;
    background-color: #274c77;
    height: 3px;
    width: 100px;
}
.line-behind-name {
    margin: auto;
    border: none;
    background-color: #274c77;
    height: 3px;
    width: 20%;
}

/*Skills*/
.skills {
    text-align: center;
    padding-block: 10px;
    margin: 0 auto;
}
.skill-subheader {
    padding-top: 10px;
    padding: 10px 30px 0 30px;
    margin: auto;    
    text-align: center;
    font-size: 15px;
}
.body-skills {
    display: grid;
    margin-top: 20px;
}
.card-skills {
    display: flex;
    justify-content: space-around;
    padding: 15px;
}
.card-skill-item {
    width: 300px;
}
.card-skill-item h3 {
    text-transform: uppercase;
}
.card-skill-icon {
    border: 4px solid #F2F2F2;
    border-radius: 100%;
    width: 125px;
    height: 125px;
    margin: auto;
}
.card-skill-description h3 {
    padding-top: 5px;
    color: #000;
    font-size: 20px;
}
.card-skill-description p {
    color: #333333;
    font-size: 15px;
}

/* Skill Icons */
.fa-html5 {
    padding-block: 11px;
    color: #e34c26;
    font-size: 100px;
}
.fa-css3-alt {
    padding-block: 11px;
    color: #264de4;
    font-size: 100px;
}
.fa-js-square {
    padding-block: 13px;
    color: #F0DB4F;
    font-size: 90px;
}
.fa-react {
    padding-block: 9px;
    color: #61DBFB;
    font-size: 100px;
}
.fa-git {
    padding-block: 19px;
    color: #F1502F;
    font-size: 80px;
}
.fa-npm {
    padding-block: 18px;
    color: #CC3534;
    font-size: 80px;
}

/*Contact*/
.contact {
    padding-top: 50px;
    padding-bottom: 75px;
}
.contact-header {
    text-align: center;
}
.contact-subheader {
    padding-top: 10px;
    max-width: 500px;
    padding: 10px 30px 0 30px;
    margin: auto;    
    text-align: center;
    font-size: 15px;
}
.contact-container {
    padding-top: 25px;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 15px;
    row-gap: 15px;
}
.contact-item-container {
    margin: auto;
    display: flex;
    flex-direction: row;
    width: 275px;
}
.contact-icon {
    font-size: 50px;
}
.contact-text {
    margin: auto 0 ;
    padding-left: 15px;
}
.contact-text h4 {
    text-transform: capitalize;
    font-size: 20px;
}
.contact-text p {
    color: #333333;
    margin-top: 10px;
    font-size: 15px;
}

/* contact icon */
.fa-telegram {
    color: #0088cc;
}
.fa-envelope {
    color: #896b46;
}

/*Footer*/
footer {
    background-color: #CCCCCC;
}
.footer-container {
    padding-block: 10px;
    display: flex;
    flex-direction: column-reverse;
    height: 100%;
}
.footer-copyright {
    padding-top: 10px;
}
.footer-copyright p { 
    font-weight: bold;
    font-size: 14px;
    text-align: center;
}
.footer-socmed p {
    font-weight: bold;
    text-align: center;
}
.footer-icon-container {
    display: flex;
    justify-content: center;
}
.footer-icon {
    cursor: pointer;
    padding-inline: 20px;
    font-size: 20px;
    padding-top: 5px;
}
.footer-icon a {
    color: black;
}
@media screen and (max-width: 600px){
    body {
        overflow-x: hidden;
        width: 100%;
    }
    nav {    
        justify-content: space-around;
        padding: 0;
    }
    .nav-links {
        position: absolute;
        right: 0px;
        height: 75vh;
        top: 50px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: translateX(100%);
        transition: transform 0.5s ease-in;
    }
    .nav-links.active {
        transform: translateX(0%);
    }
    .menu-icon {
        display: block;
    }
    .fa-times {
        cursor: pointer;
        font-size: 20px;
        padding: 15px;
    }
    .fa-bars {
        cursor: pointer;
        font-size: 20px;
        padding: 15px;
    }
}

@media screen and (min-width: 450px) {    
    .card-skill-item {
        text-align: center;
        width: 350px;
    }
}

@media screen and (min-width:500px) {        
    .contact-container {
        grid-row-gap: 0;
        row-gap: 0;
    }
    .contact-icon {
        font-size: 75px;
    }
}

@media screen and (min-width: 610px) {
    .home {
        height: 600px;
        background: linear-gradient(to right, #A5A5A5 0%, #A5A5A5 30%, #fff 30%, #fff 100%);
    }
    .download-button-container-small {
        display: none;
    }

    /* Home */
    .profile {
        padding: 0;
        display: flex;
        justify-content: center;
    }
    .card-profile{
        margin: 75px 20px 0 0;
    }
    .card-profile-top {
        width: 250px;
        height: 400px;
    }
    .profile-picture-container {
        padding: 30px;
    }
    .card-profile-text {
        padding: 10px;
    }
    .profile-name {
        margin-bottom: 20px;
    }
    .profile-job {
        display: block;
        text-align: center;
        margin-block: 20px;
        font-size: 16px;
    }
    .card-profile-bottom {
        width: 250px;
        height: 50px;
    }
    .profile-desc {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 30px;
        width: 300px;
    }
    .profile-desc-top h1 {
        font-size: 100px;
    }
    .profile-desc-top p {
        font-size: 18px;
        font-weight: bold;
        margin: 0;
    }
    .download-button-container-big {
        display: block;
        margin-top: 25px;
        margin-left: 50px;
    }
    .download-button {
        font-size: 18px;
    }
    .profile-text {
        margin-top: 10px;
    }

    /* skills */
    .skills {
        padding-top: 25px;
        padding-inline: 20px;
        max-width: 650px;
    }
    .skill-subheader {
        max-width: 500px;
    }
    .body-skills {
        grid-template-columns: 1fr 1fr;
    }
    .card-skills {
        padding: 0 auto;
    }
    .card-skill-item {
        width: 250px;
    }
    
    /*Contact*/
    .contact-subheader {
        font-size: 18px;
        padding-top: 50px;
        max-width: 550px;
    }
    .contact-container {
        padding-top: 0;
        padding-inline: 25px;
        grid-template-columns: 1fr 1fr;
        max-width: 1000px;
    }
    .contact-icon {
        font-size: 70px;
    }

    /* footer */
    .footer-container {
        padding: 10px 75px ;
        flex-direction: row;
        justify-content: space-between;
    }
    .footer-copyright {
        margin: auto 0;
        padding: 0;
    }
    .footer-copyright p { 
        text-align: left;
    }
    .footer-icon-container {
        justify-content: flex-end;
    }
}

@media screen and (min-width: 875px) {
    /* Home */
    .home {
        height: 650px;
        background: linear-gradient(to right, #A5A5A5 0%, #A5A5A5 40%, #fff 40%, #fff 100%);
    }
    .card-profile-top {
        width: 350px;
        height: 450px;
    }
    .profile-picture {
        width: 200px;
    }
    .profile-name {
        font-size: 16px;
        margin-bottom: 20px;
    }
    .profile-job {
        font-size: 20px;
    }
    .card-profile-bottom {
        width: 350px;
    }
    .profile-desc {
        margin-top: 55px;
        width: 400px;
        height: 500px;
    }
    .profile-desc-top h1 {
        font-size: 125px;
    }
    .download-button-container-big {
        margin-top: 40px;
        margin-left: 75px;
    }
    .profile-text {
        margin-top: 20px;
    }
    .profile-text1 {
        font-size: 18px;
    }
    .profile-text2 {
        font-size: 18px;
    }

    /* Skills */
    .skills {
        max-width: 1200px;
    }
    .body-skills {
        grid-template-columns: 1fr 1fr 1fr;
    }

    /* contact */
    .contact {
        padding-top: 75px;
        padding-bottom: 175px;
    }
    .contact-icon {
        font-size: 100px;
    }
}




